.mobile-menu {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 50%;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  transform: translateX(-50%);
  padding: 48px 40px;

  font-weight: 500;
  letter-spacing: 0.02em;
  line-height: 1.17;

  background-color: #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 2px 4px rgba(0, 0, 0, 0.08),
    0px 1px 3px rgba(0, 0, 0, 0.16);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (max-width: 479px) {
    max-width: 480px;
    max-height: 796px;
  }
}

.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.mobile-menu-btn__close {
  position: absolute;
  top: 10px;
  right: 15px;
  border: none;
  padding: 0;
  width: 40px;
  height: 40px;

  background-color: transparent;
}

.mobile-menu-btn__close-icon {
  fill: rgba(28, 83, 104, 1);
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .mobile-menu-btn__close:hover &,
  .mobile-menu-btn__close:focus & {
    fill: rgb(9, 49, 64);
  }
}

.mobile-nav-menu__item {
  &:not(:last-child) {
    margin-bottom: 32px;
  }
}
.mobile-nav-menu__link {
  font-size: 32px;
  color: rgba(28, 83, 104, 1);

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:focus,
  &:hover {
    color: rgb(9, 49, 64);
  }

  @media screen and (min-width: 480px) {
    font-size: 40px;
  }
}

.mobile-nav-menu__link--selected {
  color: rgb(9, 49, 64);
  position: relative;
  &::after {
    position: absolute;
    content: '>';
    width: 15px;
    height: 15px;
    right: -20px;
    top: 6px;
  }
}
